import React from "react";
import { store, useStore } from "../../constants/hookstore";
import { Link } from "react-router-dom";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import IconButton from "@material-ui/core/IconButton";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import { makeStyles } from "@material-ui/core/styles";
import firebase from "../../firebase";
import { auth } from "../../firebase";

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "center",

    //overflow: "hidden",
    height: 50,
    backgroundColor: theme.palette.background.paper
  },

  contphot: {
    margin: "5px 25px 10px 25px"

    // transform: 300,

    // "&hover": {
    //   transform: 295
    // }
  },
  photo: {
    width: 300,
    transition: ".3s ease-in-out",

    "&:hover": {
      transform: "scale(0.99)",
      opacity: 0.5
    }
  },

  titleBar: {
    background:
      "linear-gradient(to bottom, rgba(0,0,0,0) 0%, " +
      "rgba(0,0,0,0) 70%, rgba(0,0,0,0) 100%)"
  },
  icon: {
    color: "white"
  }
}));

const IMG_BASE_URL = "https://image.tmdb.org/t/p/w500";

const DEFAULT_PLACEHOLDER_IMAGE =
  "https://m.media-amazon.com/images/M/MV5BMTczNTI2ODUwOF5BMl5BanBnXkFtZTcwMTU0NTIzMw@@._V1_SX300.jpg";
let userId = null;

store.state = "";

const Movie = ({ movie }) => {
  const classes = useStyles();
  const [MovieId, movieId] = useStore("");
  const user = auth.currentUser;
  let db = firebase.firestore();

  let favorite = movie.original_title;

  firebase.auth().onAuthStateChanged(function(user) {
    if (user) {
      userId = user.uid;
    } else {
      console.log("no user logged in");
    }
  });

  console.log(movie.id);
  console.log("this is movie id");

  const poster =
    movie.poster === "N/A"
      ? DEFAULT_PLACEHOLDER_IMAGE
      : IMG_BASE_URL + movie.poster_path;

  const getMovieId = () => {
    movieId(movie.id);
  };

  const addFavorite = () => {
    // console.log(favbut, "button state add");
    //setFavbut(false);
    {
      user &&
        db
          .collection("users")
          .doc(userId)
          .update({
            favorites: firebase.firestore.FieldValue.arrayUnion(favorite)
          });
    }
    {
      !user && console.log("user needs to login");
    }
  };

  return (
    <React.Fragment>
      <div className={classes.contphot}>
        <GridListTile
          key={movie.id}

          // cols={movie.featured ? 3 : 1}
          // rows={movie.featured ? 3 : 1}
        >
          <Link to='./MovieDetail' onClick={getMovieId} value={movie.id}>
            <img className={classes.photo} src={poster} alt={movie.title} />
          </Link>
          <GridListTileBar
            title={movie.title}
            titlePosition='top'
            actionIcon={
              <IconButton
                aria-label={`star ${movie.title}`}
                className={classes.icon}
                onClick={addFavorite}
              >
                <StarBorderIcon />
              </IconButton>
            }
            titlePosition='bottom'
            actionPosition='left'
            className={classes.titleBar}
          />
        </GridListTile>
      </div>
    </React.Fragment>
  );
};

export default Movie;
